.works {
    width: 100vw;
    background-color: #25262a;
    padding: 4rem;
    h2 {
        margin: 2rem;
        text-align: center;
        background: -webkit-linear-gradient(#ecdbb6, #cda55f);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }
    .project-container {
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
        .project {
            cursor: pointer;
            margin: 1rem;
            width: 400px;
            background-color: #2d2e32;
            border-radius: 1rem;
            padding: 2rem;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            box-shadow: 0 10px 10px rgba($color: #000000, $alpha: 0.1);
            .tools {
                color: #cda55f;
                display: flex;
                justify-content: space-evenly;
                flex-wrap: wrap;
                div {
                    margin: 0.5rem;
                    background-color: #25262a;
                    padding: 0.5rem;
                    border-radius: 0.5rem;
                    box-shadow: 0 5px 5px rgba($color: #000000, $alpha: 0.1);
                }
            }
            &:hover {
                box-shadow: 10px 10px 10px rgba($color: #000000, $alpha: 0.3);
            }
            h3 {
                margin: 2rem;
                text-align: center;
                background: -webkit-linear-gradient(#ecdbb6, #cda55f);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
            }
            img {
                width: 350px;
                margin: 1rem 0;
                border-radius: 0.1rem;
                display: inline-block;
                box-shadow: 0 10px 10px rgba($color: #000000, $alpha: 0.2);
                border-radius: 0.5rem;
            }
        }
    }

    button {
        background-color: #64f4ab;
        color: #000;
        padding: 0.5rem 1rem;
        border-radius: 0.5rem;
        font-size: 1.1rem;
        border: none;
        box-shadow: 0 10px 10px rgba($color: #000000, $alpha: 0.2);
        display: block;
        margin: 2rem auto;
        transition: all 0.3s ease-in-out;
        &:hover {
            box-shadow: none;
            border: 1px solid #64f4ab;
            color: #64f4ab;
            background-color: #25262a;
        }
    }
}
